            animation: satellite-path 30s linear infinite;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    background-color: rgba(255, 255, 255, 0.8);
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    height: 1px;
    width: 1px;
}
body[data-mountain="earth"] .earth-satellite:after {
      -webkit-animation: satellite-blink 1200ms infinite;
              animation: satellite-blink 1200ms infinite;
      background-color: red;
      display: block;
      content: '';
      width: 1px;
      height: 1px;
      -webkit-transform: translateY(1px);
              transform: translateY(1px);
}
body[data-mountain="earth"] .earth-satellite-1 {
    top: 15vh;
    left: 50vh;
    -webkit-transform-origin: -2vw 28vh;
            transform-origin: -2vw 28vh;
}
body[data-mountain="earth"] .earth-satellite-2 {
    -webkit-animation-direction: reverse;
            animation-direction: reverse;
    top: 10vh;
    left: 30vh;
    -webkit-transform-origin: 8vw 22vh;
            transform-origin: 8vw 22vh;
}
